import SelectOption from '@/shared/components/SelectOption/SelectOption.vue';

export default defineComponent({
  name: "com203002sc00",
  components: {
    BreadCrumb,
    Pagination,
    SelectOption
  },
  data() {
    const staffListData = ref<StaffInfo[]>([]);
    const dataTable = ref<StaffInfo[]>([]);
    const header = [
      { columnName: this.$t('com203002sc00.column.no') },
      { columnName: this.$t('com203002sc00.column.view') },
      { columnName: this.$t('com203002sc00.column.employee_no') },
      { columnName: this.$t('com203002sc00.column.first_name') },
      { columnName: this.$t('com203002sc00.column.last_name') },
      { columnName: this.$t('com203002sc00.column.status') },
      { columnName: this.$t('com203002sc00.column.user_type') },
      { columnName: this.$t('com203002sc00.column.work_status') },
      { columnName: this.$t('com203002sc00.column.branch_code') },
      { columnName: this.$t('com203002sc00.column.branch_name') },
      { columnName: this.$t('com203002sc00.column.job_title') },
      { columnName: this.$t('com203002sc00.column.phone_no') },
      { columnName: this.$t('com203002sc00.column.email_address') },
      { columnName: this.$t('com203002sc00.column.login_date') }
    ];
    const input = reactive({
      employeeNo: "",
      userFirstName: "",
      workStatusCode: "",
      userStatusCode: "",
      userTypeCode: "",
      loginDate: ""
    });
    return {
      dataTable,
      staffListData,
      header,
      input,
      COMMON_KEY_VALUE,
      totalItems: 0,
      currentPage: 1,
      pageSize: PAGE_SIZE,
      startingIndex: 1,
      isReset: false,
      breadcrumbs: [
        { value: 'apm201001sc00.label.home', keyPath: '/COM200001SC00', type: 'route' },
        { value: 'com203002sc00.portal_management', keyPath: '', type: 'label' },
        { value: 'com203002sc00.bank_staff_info', keyPath: '', type: 'label' },
        { value: 'com203002sc00.bank_staff_list', keyPath: '', type: 'route' }
      ]
    };
  },
  mounted() {
    this.handleFetchStaffList();
  },

  computed:{
    userStatusCodeValue() {
      return (statusCode: string) => `${statusCode}` +' : '+ this.$codeConverter.convertValue('userStatusCode', `${statusCode}`);
    },

    workStatusCodeValue() {
      return (statusCode: string) => `${statusCode}` +' : '+ this.$codeConverter.convertValue('WorkStatusTypeCode', `${statusCode}`); 
    },

    userTypeCodeValue() {
      return (typeCode: string) => `${typeCode}` +' : '+ this.$codeConverter.convertValue('userTypeCode', `${typeCode}`); 
    }
  },

  methods: {
      // Convert status Code 
      workStatusConverter(statusCode): string {
      switch (statusCode) {
        case '00: Registered':
          return 'success'
        case '01: Normal':
          return 'info'
        case '02: Dispatched':
          return 'fail'
        case '03: Leave':
          return 'info'
        case '04: Suspended':
          return 'warning'
        case '05: Quit':
          return 'info'
        default: '06: Ect'
          return 'fail'
      }
    },

    statusConverter(statusCode): string {
      switch (statusCode) {
        case '01: Active':
          return 'success'
        case '02: Suspended':
          return 'info'
        default: '03: Deleted'
          return 'fail'
      }
    },
    handleClickRegister() {
      this.$router.push({
        path: '/COM203002SC01',
        query: {
          type: 'register'
        }
      })
    },
    handleClickRow(index: number, rowData: StaffInfo) {
      this.$router.push({
        path: '/COM203002SC01',
        query: {
          type: 'edit',
          employeeNo: rowData.employeeNo
        }
      })
    },
    // Convert Code
    convertCodeToValue(data: StaffInfo[]): StaffInfo[] {
      const newData = data.map((record: StaffInfo) => {
        record.userStatusCode = record.userStatusCode ? this.userStatusCodeValue(record.userStatusCode) : "",
        record.workStatusCode = record.workStatusCode ? this.workStatusCodeValue(record.workStatusCode) : "",
        record.userTypeCode = record.userTypeCode ? this.userTypeCodeValue(record.userTypeCode) : "",
        record.loginDate = this.$datetime.formatStringToStringDate(record.loginDate)
        return record;
      })
      return newData;
    },
