      //         return (data.employeeNo.includes(this.input.employeeNo) && data.userFirstName.toUpperCase().includes(this.input.userFirstName.toUpperCase())
      //         );
      //       } else if (this.input.workStatusCode && this.input.userStatusCode) {
      //         if (this.input.workStatusCode !== '00' && this.input.userStatusCode === '00') {
      //           return (data.employeeNo.includes(this.input.employeeNo) && data.userFirstName.toUpperCase().includes(this.input.userFirstName.toUpperCase())
      //             && this.input.workStatusCode == workStatusKey)
      //         } else if (this.input.workStatusCode === '00' && this.input.userStatusCode !== '00') {
      //           return (data.employeeNo.includes(this.input.employeeNo) && data.userFirstName.toUpperCase().includes(this.input.userFirstName.toUpperCase())
      //             && this.input.userStatusCode == userStatusKey)
      //         } else {
      //           return (data.employeeNo.includes(this.input.employeeNo) && data.userFirstName.toUpperCase()
      //             .includes(this.input.userFirstName.toUpperCase()) && this.input.workStatusCode == workStatusKey &&
      //             this.input.userStatusCode == userStatusKey)
      //         }
      //       }
      //     }
      //   })
      //   this.dataTable = searchResult
      // })
    // },
    // Clear Search List
    async handleClickRefresh() {
      this.input.employeeNo = "";
      this.input.userFirstName = "";
      this.input.workStatusCode = "";
      this.input.userStatusCode = "";
      this.isReset = true;
      this.currentPage = 1;
      await this.handleFetchStaffList()
    },
    // Key Type Search List 
    async handleFetchStaffList(mode?: string) {
      const trCode = "COM20300211"
      const bMock = false
      const body: COM20300211_REQ_BODY = {
        pageSize: this.pageSize,
        pageNumber: mode == 'search' ? 0 : this.currentPage,          //pageNumber
        employeeNo: this.input.employeeNo,          //Employee No
        userFirstName: this.input.userFirstName,    //User First Name
        userStatusCode: this.input.userStatusCode,  //User Status Code
        workStatusCode: this.input.workStatusCode //Work Status Code
      }
      if (mode === 'search') {
        this.isReset = true;
        this.currentPage = 1
      }
      const res = await this.$request.getRequest(trCode, body, bMock) as COM20300211_RES
      this.dataTable = this.onMappingTableData(res.body.list)
      this.staffListData = JSON.parse(JSON.stringify(this.convertCodeToValue(res.body.list)));
      this.totalItems = res.body.totalCount
      
    },
    handlePagination(pageNumber: number) {
      this.isReset = false;
      this.currentPage = pageNumber
      this.handleFetchStaffList()
    },
    handleStartingIndex(startingIndex: number) {
      this.startingIndex = startingIndex
    },
    
    onMappingTableData(lists) {
      const { UserStatusCodes, UserTypeCode, WorkStatusCode } = this.$store.state.CommonCodeModule.commonCode;
      return lists.map((item, index) => ({
        No: index + 1,
        branchCode: item.branchCode,
        branchName: item.branchName,
        cellPhoneNo: item.cellPhoneNo,
        emailAddress: item.emailAddress,
        employeeNo: item.employeeNo,
        loginDate: this.$datetime.formatStringToStringDate(item.loginDate),
        userFirstName: item.userFirstName,
        userLastName: item.userLastName,
        jobTitleCode: item.jobTitleCode,
        userStatusCode: UserStatusCodes && item.userStatusCode ? `${UserStatusCodes.find((type) => type.key === item.userStatusCode)?.key}: ${UserStatusCodes.find((type) => type.key === item.userStatusCode)?.value}` : '',
        userTypeCode: UserTypeCode && item.userTypeCode ? `${UserTypeCode.find((type) => type.key === item.userTypeCode)?.key}: ${UserTypeCode.find((type) => type.key === item.userTypeCode)?.value}` : '',
        workStatusCode: WorkStatusCode && item.workStatusCode ? `${WorkStatusCode.find((type) => type.key === item.workStatusCode)?.key}: ${WorkStatusCode.find((type) => type.key === item.workStatusCode)?.value}` : '',
      }));
    },
  },
